.footer {
  padding: 33px 18px;
  background-color: $page-bgcolor-base;

  @media (min-width: $width-tablet) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.footer__text {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-family: $font-base;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
  color: #000000;
}

.footer__social {
  margin-bottom: 30px;
}
